@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;900&display=swap);
body {
   margin: 0;
   overflow: hidden;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}
:root {
   --win95-bg-colour: #c3c7cb;
   --win95-dark-blue: rgb(7, 30, 129);
   --win95-light-blue: #2b52ff;
}
body.blurred::after {
   content: "";
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   -webkit-backdrop-filter: blur(2px);
           backdrop-filter: blur(2px);
   z-index: 2;
}

#version-text {
   --offset: 0.3rem;
   color: #ddd;
   font: 400 0.95em "Source Code Pro";
   position: absolute;
   top: var(--offset);
   left: var(--offset);
   text-shadow: 2px 2px 0 #000;
}

.hidden {
   display: none !important;
}

audio {
   display: none;
}


button, .button {
   outline: none;
   cursor: pointer;
}

.cf:before,
.cf:after {
  content: " ";
  display: table;
}
.cf:after {
  clear: both;
}

#mask {
   background-color: rgba(0, 0, 0, 0.4);
   position: absolute;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
   z-index: 3;
}

.back-button {
   color: #fff;
   font: 700 1.1rem "PT Serif";
   background-color: rgb(255, 84, 17);
   padding: 0.1rem 1rem;
   border: 1px solid rgb(255, 253, 228);
   margin: 0.5rem 0 1rem;
   box-shadow: 0 0 0 1px rgb(184 128 54), 0 0 6px 1px rgb(0 0 0 / 40%) inset;
   cursor: pointer;
   position: absolute;
   bottom: 1rem;
   left: 50%;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
}
.back-button:hover {
   box-shadow: none;
}
.back-button:active {
   color: rgb(235, 233, 210);
   border-color: rgb(235, 233, 210);
   background-color: rgb(226, 81, 24);
   box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.3) inset, 0 0 8px 1px #000;
}
#notification-container {
   position: absolute;
   left: 50%;
   bottom: 2.5rem;
   display: inline-block;
   -webkit-transform: translate(-50%);
           transform: translate(-50%);
   z-index: 1;
   pointer-events: none;
}
#notification-container * {
   pointer-events: all;
}
.notification {
   font: normal 1rem "MS Sans Serif";
   background-color: #ddd;
   border: 2px solid;
   border-top-color: #fff;
   border-right-color: #000;
   border-bottom-color: #000;
   border-left-color: #fff;
   padding: 0.2rem;
   margin-left: 50%;
   margin-top: 0.4rem;
   display: table;
   position: relative;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
   white-space: nowrap;
   box-shadow: 4px 4px 0 0 rgba(0, 0, 0, 0.4);
}
.notification .top {
   height: 2rem;
   margin-bottom: 0.4rem;
   display: flex;
}
.notification img:not(.ui-button) {
   width: 32px;
   height: 32px;
}
.notification .title {
   line-height: 2rem;
   font-size: 0.95rem;
   font-weight: bold;
   margin: 0 1.8rem 0 0.2rem;
}
.notification .description {
   color: #333;
   font-size: 0.9rem;
   font-style: italic;
   margin: 0;
}
.notification .seperator {
   width: 100%;
   height: 2px;
   background-color: #000;
   margin: 0.5rem 0 0.2rem;
   box-shadow: 0 2px 0 0 #fff;
}
.notification .caption {
   text-align: center;
   color: #888;
   font-weight: bold;
   font-size: 0.9rem;
   margin: 0;
}
.notification .caption.flashing {
   -webkit-animation: CaptionFlash 1s infinite;
           animation: CaptionFlash 1s infinite;
}
@-webkit-keyframes CaptionFlash {
   0% {
      color: #888;
   }
   50% {
      color: red;
   }
}
@keyframes CaptionFlash {
   0% {
      color: #888;
   }
   50% {
      color: red;
   }
}
.notification.clickable .caption:hover {
   color: #000;
   background-color: #e5e5e5;
   cursor: pointer;
}
.notification.clickable .caption:active {
   color: #555;
   background-color: #e0e0e0;
   cursor: pointer;
}
.notification .ui-button {
   --offset: 2px;
   position: absolute;
   top: var(--offset);
   right: var(--offset);
}

.notification.closing {
   -webkit-animation: NotificationClose 0.4s cubic-bezier(.14,.7,.34,.86) forwards;
           animation: NotificationClose 0.4s cubic-bezier(.14,.7,.34,.86) forwards;
}
@-webkit-keyframes NotificationClose {
   0% {
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      opacity: 1;
   }
   100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
      opacity: 0;
   }
}
@keyframes NotificationClose {
   0% {
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      opacity: 1;
   }
   100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
      opacity: 0;
   }
}

#notification-container button {
   cursor: pointer;
}
