/* INBOX */
#inbox {
   width: 30rem;
   top: 50%;
   left: 25%;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
   z-index: 4;
}
#inbox .title-bar {
   margin-bottom: 0.5rem;
}
#inbox .formatter {
   display: flex;
}

/* Folders */
#inbox .folder-container {
   height: 10rem;
}
#inbox .folder {
   --image-size: 22px;
   --height: 24px;
   height: var(--height);
   margin: 0;
   position: relative;
}
#inbox .folder:not(.opened):active {
   background-color: #f5f5f5;
}
#inbox .folder:not(.opened) {
   cursor: pointer;
}
#inbox .folder.opened::after {
   content: "";
   width: 5px;
   height: 5px;
   border-top: 2px solid #555;
   border-right: 2px solid #555;
   position: absolute;
   top: 50%;
   right: 5px;
   -webkit-transform: translateY(-50%) rotate(45deg);
           transform: translateY(-50%) rotate(45deg);
}
#inbox .folder .icon {
   width: var(--image-size);
   height: var(--image-size);
   position: absolute;
   top: 50%;
   -webkit-transform: translateY(-50%);
           transform: translateY(-50%);
   z-index: 1;
   pointer-events: none;
}
#inbox .folder .icon img {
   width: var(--image-size);
   height: var(--image-size);
}
#inbox .folder span {
   margin-left: calc(var(--image-size) + 4px);
}
#inbox .folder.has-unopened-letters span {
   color: red;
}
#inbox .folder.has-unopened-letters .icon::after {
   --size: 16px;
   content: "";
   width: var(--size);
   height: var(--size);
   background-image: url(data:image/png;base64,);
   background-size: var(--size) var(--size);
   position: absolute;
   top: 50%;
   /* left: 0; */
   left: 50%;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
   -webkit-animation: folderIconFlash 1.5s ease-in-out infinite;
           animation: folderIconFlash 1.5s ease-in-out infinite;
   /* z-index: 999; */
}
@-webkit-keyframes folderIconFlash {
   0% {
      opacity: 1;
   }
   50% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}
@keyframes folderIconFlash {
   0% {
      opacity: 1;
   }
   50% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}
#inbox .root-folder .icon {
   background-image: url(data:image/png;base64,) !important;
}
#inbox .folder span {
   line-height: var(--height);
   margin-left: 30px;
   pointer-events: none;
}
#inbox .folder.opened span {
   font-weight: bold;
}
#inbox .folder:not(.root-folder) {
   margin-left: 20px;
}

/* Letter section */
#inbox .scroll-area {
