   top: 50%;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
}
#inbox .ui-section-container .ui-section:first-child {
   width: 50px;
   position: relative;
}
#inbox .ui-section-container img {
   width: 16px;
   position: absolute;
   left: 50%;
   top: 50%;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
}
#inbox .ui-section-container .ui-section:first-child {
   width: 50px;
   position: relative;
}
#inbox .letter {
   width: 100%;
   display: flex;
   cursor: pointer;
   position: relative;
}
#inbox .letter.opened {
   color: #fff;
   font-weight: bold;
   background-color: var(--win95-dark-blue);
   margin: -2px 0 -2px -2px;
   border: 2px dotted #fff;
   cursor: default;
}
#inbox .letter * {
   margin: 0 2px;
}
#inbox .letter div.icon-container {
   width: 50px;
   position: relative;
}
#inbox .letter img {
   width: 20px;
   position: absolute;
   top: 50%;
   left: 50%;
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
}
#inbox .letter.unopened {
   -webkit-animation: unopenedLetterFlash 2s infinite linear;
           animation: unopenedLetterFlash 2s infinite linear;
}
@-webkit-keyframes unopenedLetterFlash {
   0% {
      background-color: #fff;
   }
   49% {
      background-color: red;
   }
   50% {
      background-color: red;
   }
   90% {
      background-color: #fff;
   }
}
@keyframes unopenedLetterFlash {
   0% {
      background-color: #fff;
   }
   49% {
      background-color: red;
   }
   50% {
      background-color: red;
   }
   90% {
      background-color: #fff;
   }
}
#inbox .letter.unopened::after {
   content: "";
   --size: 15px;
   width: var(--size);
   height: var(--size);
   background-image: url(data:image/png;base64,);
   background-size: var(--size) var(--size);
   position: absolute;
   right: 5px;
   top: 50%;
   -webkit-transform: translateY(-50%);
           transform: translateY(-50%);
}
#inbox .letter div:not(.icon-container) {
   width: 100%;
}
#inbox .letter div:not(.icon-container) {
   text-overflow: ellipsis;
   overflow: hidden;
}
#inbox .letter div:not(.icon-container) span {
   margin-left: 10px;
   white-space: pre;
}



